import React, { useState, useEffect } from "react";

const CustomerDashboard = () => {
  const [customerData, setCustomerData] = useState(null);

  useEffect(() => {
    // Fetch customer-specific data (e.g., order history, recommendations)
    const fetchCustomerData = async () => {
      const token = localStorage.getItem("token");
      try {
        const response = await fetch("http://localhost:5000/api/customer", {
          method: "GET",
          headers: {
            "Authorization": `Bearer ${token}`,
            "Content-Type": "application/json",
          },
        });

        const data = await response.json();
        if (response.ok) {
          setCustomerData(data);
        } else {
          console.log("Error fetching customer data:", data.message);
        }
      } catch (err) {
        console.error("Error:", err);
      }
    };

    fetchCustomerData();
  }, []);

  if (!customerData) return <div>Loading customer data...</div>;

  return (
    <div>
      <h1>Customer Dashboard</h1>
      <div>
        <h2>Welcome, {customerData.name}!</h2>
        {/* Customer specific content */}
        <p>Your order history: {customerData.orderHistory}</p>
        <p>Recommended for you: {customerData.recommendations}</p>
      </div>
    </div>
  );
};

export default CustomerDashboard;
